<script>
	import "../assets/js/jquery.cookie.js"

	export default {
		data() {
			return {
				money: '',
				zjf: '',
				zhuat: '',
				centre_id: '',
				hd_id: '',
				openid: '',
				parentopenid: '',
				num: null, //
				isShow: false, //
			}
		},
		mounted() {
			this.centre_id = $.cookie("centre_id")
			this.hd_id = $.cookie('hd_id')
			this.openid = $.cookie("openid")
			this.parentopenid = $.cookie("parentopenid")
			$.cookie('isManden', '1', {
				expires: 300
			})

			//
			this.red_packet()

			var self = this;
			$.ajax({
				"url": "https://hdapi.codeclassroom.org/index/hd/flrw",
				type: "post",
				dataType: 'json',
				data: {
					openid: self.openid,
					hd_id: self.hd_id
				},
				success: function(data) {
					self.money = data.data.money;
					self.zjf = data.data.zjf;
					self.num = data.red
				}
			});

			$.ajax({
				"url": "https://hdapi.codeclassroom.org/index/hd/hq",
				type: "post",
				dataType: 'json',
				data: {
					openid: self.openid,
				},
				success: function(data) {
					if (data == 2) {
						$('#as3').modal({
							"backdrop": "static"
						})
					}
				}
			});

			//
			record.get_record(this.hd_id, this.parentopenid, this.openid, '', this.centre_id)
		},
		destroyed() {
			$('#as3').modal('hide')
			$('#myModa').modal('hide');
			$('#myModa2').modal('hide')
			$('#myModa3').modal('hide')
			$('.modal-backdrop').remove() //
			window.location.reload()
		},
		methods: {
			red_packet() {
				let that = this;
				$.ajax({
					"url": "https://hdapi.codeclassroom.org/index/hd/is_flrw",
					type: "post",
					dataType: 'json',
					data: {
						hd_id: that.hd_id
					},
					success: function(data) {
						if (data.status == 2) {
							that.isShow = true
						}
					}
				});
			},
			hongbao: function() {
				var self = this;
				$('#myModal').modal({
					"backdrop": "static"
				})
				$.ajax({
					"url": "https://hdapi.codeclassroom.org/index/hd/lqhb",
					type: "post",
					dataType: 'json',
					data: {
						openid: self.openid,
						hd_id: self.hd_id
					},
					success: function(res) {
						self.zhuat = res.zhuat
						self.$toast(res.msg)
					}
				});
			},
			lingqu: function() {
				$('#myModa2').modal({
					"backdrop": "static"
				})
			},
			youhui: function() {
				$('#myModa3').modal({
					"backdrop": "static"
				})
			},
			linq: function() {
				var self = this;
				$.ajax({
					"url": "https://wx.codeclassroom.org/pay/redpackhdnew.php",
					type: "post",
					dataType: 'json',
					data: {
						openid: self.openid,
						hd_id: self.hd_id,
						centre_id: self.centre_id,
					},
					success: function(data) {
						if (data == 1) {
                            self.$toast.fail('')
							self.zhuat = 3
						} else if (data == 2) {
							self.$toast.fail('')
						} else {
							self.$toast.fail('')
						}
					}
				});
			}
		},
	}
</script>

